import { Component, EventEmitter, Input, Output } from '@angular/core';
import { FormControl, FormGroup, Validators } from '@angular/forms';
import { VhAlgorithm, VhAuth, VhImage } from 'ionic-vhframeworks';
import { LanguageService } from 'src/app/sales/services/language.service';
import { VhComponent } from 'src/app/sales/components/vh-component/vh-component';
import { FunctionService } from 'src/app/sales/services/function.service';
import { CountryCode } from 'libphonenumber-js/max';


@Component({
  selector: 'app-account-information',
  templateUrl: './account-information.component.html',
  styleUrls: ['./account-information.component.scss']
})
export class AccountInformationComponent {
  @Input() modalAccountInfo: boolean = false
  @Output() closeAccount = new EventEmitter<any>();
  /** form lưu các trường của nv đăng nhập */
  detailEmployee: FormGroup;
  employee: any;



  constructor(
    public languageService: LanguageService,
    private vhImage: VhImage,
    public vhAlgorithm: VhAlgorithm,
    private vhComponent: VhComponent,
    private vhAuth: VhAuth,
    private fncService: FunctionService,
  ) {
    this.employee = this.vhAuth.getlocalEmployee(this.vhAuth.getUser()._id);
    this.countryCode = this.employee.countryCode ?? this.fncService.defaultCountryCode;
    this.callingCode = this.fncService.getCallingCodeFromCountryCode(this.countryCode) ?? this.fncService.defaultCallingCode;
    this.formattedPhone = this.fncService.getFormattedPhoneNumber(this.employee.phone, this.countryCode as CountryCode);

    this.detailEmployee = new FormGroup({
      img: new FormControl(this.employee.img),
      name: new FormControl(this.employee.name, Validators.compose([Validators.required, Validators.compose([Validators.minLength(6)])])),
      email: new FormControl(this.employee.email, Validators.email),
      password: new FormControl(this.employee.password, Validators.compose([Validators.minLength(5), Validators.required])),
      phone: new FormControl(this.formattedPhone, [Validators.required, this.fncService.phoneValidator(() => this.countryCode)]),
      gender: new FormControl(this.employee.gender),
      address: new FormControl(this.employee.address),
      dateofbirth: new FormControl(this.employee.dateofbirth),
      datework: new FormControl(this.employee.datework),
      contract: new FormControl(this.employee.contract),
    });
  }

  getFile() {
    document.getElementById("file-upload").click();
  }

  /** 
   * Lấy hình ảnh từ Desktop
   * @example this.onUpload($event)
   */
  onUpload(e?) {
    const file = e.target.files[0];
    this.vhImage.getThumbnailFromDesktop(file, 256, 256, "employees", this.detailEmployee.value["img"] || '').then(
      photoURL => {
        this.detailEmployee.controls["img"].setValue(photoURL);
        this.vhComponent.showToast(2000, (this.languageService.translate("Image was loaded successfully")), 'success-toast');
        this.updateEmployee(this.detailEmployee);
      },
      () => {
        this.vhComponent.showToast(2000, (this.languageService.translate("Image was failed")), 'alert-toast');
      }
    );
  }

  closeModal() {
    this.closeAccount.emit(false)
  }

  /**
   * Cập nhật thông tin thay đổi của nv vào database
   * @param formValue : giá trị của biến this.detailEmployee
   * @example this.updateEmployee(this.detailEmployee.value)
  */
  updateEmployee(formValue) {
    if (formValue.valid) {
      let value = { ...formValue.value };
      if(value.phone) {
        value.countryCode = this.countryCode;
        value.phone = this.fncService.getNationalPhoneNumber(value.phone, this.countryCode);
        value.phoneNumber = this.fncService.getInternationalPhoneNumber(value.phone, this.countryCode);
      }
      this.vhAuth.updateEmployee(this.employee._id, value).catch(err => { console.error(err) })
    }
  }
  
  
  
  /** Mã quốc gia */
  countryCode: string = this.fncService.defaultCountryCode;
  /** Calling code */
  callingCode: string = this.fncService.defaultCallingCode;
  /** Số điện thoại đã format. VD: 333 333 333 */
  formattedPhone: string = '';

}
